
/* 0. Reset ================================= */
@import 'reset';
@import 'voting';
@import 'newartist';
$primary-color: #f70c0c; //red
$secondary-color: #0600ff; //blue

$primary-font: "octin-college", sans-serif;
$secondary-font: 'Source Code Pro', monospace;

/* 1. Main Layout ================================= */

*{
	box-sizing: border-box;
}

body{
	background: #fff;
	width: 100%;
	
}

a{
	font-family: $secondary-font;
}

/* 2. Content ================================= */

header{
	color: $primary-color;
	margin: 0 0 18px 2%;
		
	.logo{
		margin-top: 15px;
		display: flex;
		width: 50%;

		img{
			width: 10%;
			height: 10%;
		}

		h1{
			font-family: $primary-font;
			font-size: 2.3vw;
			padding-left: 3%;
			letter-spacing: 2px;
		}
	}

	.open-nav{
		display: flex;
		font-family: $secondary-font;
		justify-content: space-between;
		width: 35%;
		margin-left: 8%;
		margin-top: -2px;

		img{
			width: 4%;
			height: 4%;
			margin-top: -1%;


		}

		a{
			font-size: .6em;
			text-decoration: none;
			color: $primary-color;
			transition: .25s;

			&:hover{
				color: $secondary-color;
				cursor: pointer;
			}

			@media screen and (min-width: 1080px){
				font-size: 1.1em;
			}
		} 
	}
}

