.home h3{
	font-family: $primary-font;
	font-size: 1.4em;

	@media screen and (min-width: 692px){
		font-size: 2.1em;
	}
}

.home .voting-sect{
	.voting-bg{
		background: url(../img/vid-background.png) no-repeat top;
		background-size: 105%;
		position: absolute;
		height: 0;
		padding-bottom: 39%;
		margin-left: 8.5%;
		width: 78%;
		z-index: -2;	
	}

	.vote-left{
		position: relative;
		padding-top: 4%;
		z-index: 2;
		width: 41%;

		h3{
			color: $primary-color;
			
			&:nth-child(1){
				opacity: 0;
				animation: ani-voting 2s infinite;
			}

			&:nth-child(2){
				opacity: 0;
				animation: ani-voting 2s infinite;
				animation-delay: .5s;
			}

			&:nth-child(3){
				opacity: 0;
				animation: ani-voting 2s infinite;
				animation-delay: 1s;
			}
		}
	}

	@keyframes ani-voting {
		0% {
			opacity: 0;
		}
		33%{
			opacity: 1;
		}
		66% {
			opacity: 0;
		}
		99% {
			opacity: 0;
		}
	}

	video{
		position: absolute;
		width: 64%;
		margin: -15% 16%;
		z-index: 1;
	}

	.vote-right{
		position: relative;
		padding-top: 4%;
		padding-right: 2%;
		float: right;
		z-index: 2;

		h3{
			color: $secondary-color;

			&:nth-child(1){
				opacity: 0;
				animation: ani-voting 2s infinite;
				animation-delay: .5s;
			}

			&:nth-child(2){
				opacity: 0;
				animation: ani-voting 2s infinite;
				animation-delay: 1s;
			}

			&:nth-child(3){
				opacity: 0;
				animation: ani-voting 2s infinite;
				animation-delay: 1.5s;
			}
		}	
	}		
}

.home .video-library-sect{
	width: 100%;
	padding-top: 28%;

	h3{
		color: $secondary-color;
		margin-bottom: 4%;
	}

	a{
		font-size: .3em;
		color: $primary-color;
		padding-left: 3%;
		// margin-top-top: 2px;
	}

	div{
		display: flex;

		figure{
			filter: grayscale(100%);
			transition: .5s;
			overflow: hidden;
			height: 0;
			padding-bottom: 47%;
			width: 100%;

			&:hover{
				filter: none;
			}

			figcaption{
				background: rgba(255, 0, 0, .8);
				color: #fff;
				padding: 4% 4% 10% 4%;
				display: flex;
				flex-direction: column;
				font-family: $secondary-font;
				margin-top: 102%;
				display: none;
				transition: .5s;
				opacity: 0;

				h4{
					background: $secondary-color;
					width: 19%;
					padding: 1% 4%;
					font-size: 1.3vw;
					transform: translateX(0%);
					animation: ani-titles .5s;
				}

				h5{
					font-size: 1.7vw;
					text-transform: uppercase;
					padding: 5% 0;
					transform: translateX(0%);
					animation: ani-titles .5s;
					// animation-delay: .25s;
				}

				a{
					color: $secondary-color;
					float: right;
					transform: translateX(0%);
					animation: ani-titles .5s;
					// animation-delay: .75s;
				}

				@keyframes ani-titles {
					0% {
						transform: translateX(-105%);
					}
					100% {
						transform: translateX(0%);			}
				}	
			}
		}

		.justin-video-of-the-year{
			background: url(../img/justin-video.jpg) top no-repeat;
			background-size: cover;
		}

		.gaga-video-of-the-year{
			background: url(../img/gaga-video.jpg) top no-repeat;
			background-size: cover;
		}

		.fiona-video-of-the-year{
			background: url(../img/fiona-video.jpg) top no-repeat;
			background-size: cover;
		}
	}
}

